import React from 'react'
import Background from '../img/banner.jpg'

const HeroContent = () => {

    return (
        <div id="hero" class="bg-cover bg-center" style={{ backgroundImage: `url(${Background})` }}>
            <div class="bg-gradient-to-b from-transparent to-black w-full md:h-screen">
                <div class="text-center p-5">
                    <div class="pt-40 px-12">
                        <h1 class="text-white text-4xl lg:text-5xl mb-6 font-semibold">Desarrollo Web a la medida de tus
                        necesidades</h1>
                        <p class="text-white mb-8 text-lg font-extralight">Una tienda? Una necesidad?, un Negocio?
                        Platicanos del
                        proyeco que tienes en mente, paso a paso te ayudamos a cumplirlo
                        </p>
                        <a class="border-solid border-2 font-semibold border-white px-3 py-1 text-white rounded-lg  mr-2"
                            href="#">Read
                        More</a>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default HeroContent
